:root {
    font-size: calc(.5em + 1vw);
    box-sizing: border-box;

    /* Variables */
    --background-color-main: #232329;
    --color-main: #FAFFFD;
    --color-orange: #FA824C;
    --color-blue: #3C91E6;
    --color-green: #A2D729;
    --color-watermark: #707070;
}

::-webkit-scrollbar {
    width: 7.5px;
}

::-webkit-scrollbar-thumb {
    background-color: var(--color-blue);
    border-radius: 20px;
    border: 6px solid transparent;
}

::-webkit-scrollbar-thumb:hover {
    background-color: var(--color-orange);
}

@media only screen and (max-width: 768px) {
    #time-to {
        position: relative;
        bottom: .75em;
    }

    .break-time {
        top: -1.55em;
    }

    #tasks-container {
        top: 1em;
        display: block;
        margin-left: auto;
        margin-right: calc(29em + 1vw);
    }
}

@media only screen and (max-width: 300px) {
    #tasks-container {
        display: block;
        left: 0;
    }

    #quote {
        position: relative;
        top: 2em;
    }
}

*, ::before, ::after {
    box-sizing: inherit;
}

body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: var(--background-color-main);
    color: var(--color-main);    
    margin: 0;
    padding: 0;
    overflow-x: hidden;
}

body *+* {
    margin-top: 1.5em;
}

a {
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
    text-decoration-color: var(--color-orange);
}

#container {
    min-width: 17em;
    text-align: center;

    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 5em;
}

#time-to {
    margin-bottom: -1em;
}

#clock {
    font-size: 5em;
    cursor: pointer;
}

#start-pause {
    cursor: pointer;
    position: relative;
    bottom: 0.7em;
    font-size: 1.1em;
    width: 1em;
    height: 1em;
    padding: 0.4em;
    border-radius: 10px;
}

#pomodoro-watermark {
    color: var(--color-watermark);
    margin-bottom: 0;
    font-size: 0.9em;
    position: relative;
    top: 0.5em;
    width: 8em;

    display: block;
    margin-left: auto;
    margin-right: auto;
}

#reset {
    color: var(--color-orange);
    position: relative;
    bottom: 0.5em;
    cursor: pointer;
    font-size: 1.1em;
}

.controllers img {
    width: 0.8em;
    filter: invert(75%);
    cursor: pointer;
    transform: rotate(90deg);

    margin-left: 0.3em;
    margin-right: 0.3em;
    margin-top: .8em;

    opacity: 0.4;
    transition: 0.15s;
}

@keyframes colorChange {
    0% {
        opacity: 1;
    }
    100% {
        filter: invert(62%) sepia(22%) saturate(1581%) hue-rotate(328deg) brightness(99%) contrast(99%);
        opacity: 1;
    }
}

.controllers img:hover {
    animation-name: colorChange;
    animation-duration: 0.5s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

.controller {
    position: relative;
    font-size: 1.2em;
}

.work-time {
    position: relative;
    right: 4.5em;
    top: 1.5em;
}

.break-time {
    position: relative;
    bottom: 1.70em;
    left: 4.5em;
}

/* Tasks Below */

#tasks-container {
    position: relative;
    margin-top: 0;
    bottom: 23em;
    color: var(--color-blue);
    left: 1.2em;
    max-height: 80vh;
    display: block;
    width: calc(20em + 1vw);
    padding: .75em;
    border-radius: 10px;
}

.input-text {
    position: relative;
    bottom: 1em;
    background-color: transparent;
    color: #eeeeee;
    outline: none;
    outline-style: none;
    border-top: none;
    border-left: none;
    border-right: none;
    border-bottom: solid #eeeeee 1px;
    padding: 0 5.5em;
    opacity: 0.5;
    margin-bottom: 1em;
}

.input-text:hover {
    border-bottom-color: var(--color-blue);
    opacity: 1;
}

#trash-bin {
    margin-left: .2em;
    position: relative;
    top: 0.13em;
    opacity: 0.7;
    cursor: pointer;
    width: 1em;
    filter: invert(62%) sepia(22%) saturate(1581%) hue-rotate(328deg) brightness(99%) contrast(99%);
}

#trash-bin:hover {
    opacity: 1;
}

#todos {
    margin-top: -1em;
    font-size: 0.8em;
}

#todos li {
    list-style: none;
}

#todos #todo {
    color: var(--color-green);
    position: relative;
    bottom: 1em;
    right: 2.5em;
    font-size: 1em;
}

#todos #delete-icon, #check-icon, #undo-icon {
    opacity: 0.4;
    position: relative;
    top: 0.15em;
    margin-right: .35em;
    cursor: pointer;
    width: 1em;
}

#todos #delete-icon {
    top: 0.25em;
    filter: invert(30%) sepia(67%) saturate(7242%) hue-rotate(359deg) brightness(100%) contrast(115%);
}

#todos #check-icon {
    top: 0.15em;
    filter: invert(66%) sepia(79%) saturate(441%) hue-rotate(33deg) brightness(105%) contrast(85%);
}

@keyframes rotate {
    0% {
        transform: rotate(0);
    }
    100% {
        transform: rotate(360deg);
    }
}

#undo-icon {
    margin-left: .02em;
    filter: invert(62%) sepia(22%) saturate(1581%) hue-rotate(328deg) brightness(99%) contrast(99%);
}

#undo-icon:hover {
    animation-name: rotate;
    animation-duration: 0.5s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

#todos #check-icon:hover, #delete-icon:hover, #undo-icon:hover {
    opacity: 0.7;
}

#todos #current {
    color: var(--color-blue);
    position: relative;
    margin-top: -1em;
    margin-left: 1.27em;
    right: 1.1em;
    bottom: 1em;
    cursor: pointer;
}

#margin-bottom {
    margin-top: -1000em;
}
